import axios, { allFRLogs } from '../../axios'
import React from 'react'
import { useState } from 'react'
import { useEffect } from 'react'
import face1 from './../../assets/face1.png'
import { dummyContent } from '../constants'

const AllVisitors = () => {

    const [visitor, setVisitor] = useState([])
    const [loaded, setLoaded] = useState(false);
    const [errHandler, setErrHandler] = useState(false)
    useEffect(() => {
        const fetchVisitors = () => {
            axios.post(allFRLogs, {
                "FromDate": "2022-10-03 12:32:10.000",
                "ToDate": "2022-10-03 18:59:52.000"
            }).then((res) => {
                setVisitor(res.data.Result)
                // console.log("🚀 All Visitors")
                setErrHandler(false)
            }).catch((err) => {
                console.log(err);
                setErrHandler(true)
            }).finally(() => {
                setLoaded(true)
                setErrHandler(false)
            })
        }
        fetchVisitors()
        const interval = setInterval(() => {
            fetchVisitors()
        }, 6000);
        return () => clearInterval(interval)

    }, [])
    return (<>
        {errHandler ? dummyContent.map((obj) => {
            return (
                <div key={obj.id} className='Card AllVisitors'>
                    <div className='align-items-center d-flex'>
                        <img src={face1} className="img-fluid align-self-center" style={{ "width": "4rem", "borderRadius": "10rem" }} alt="" />
                        <div className='ms-4'>
                            <div className='head'>Anonymous</div>
                            <div className='temp'>Temperature:NA</div>
                        </div>
                    </div>
                    <div className='align-self-center'>
                        <div>Main Entrance Registration</div>
                        <div className='success'>Active - Clear</div>
                    </div>
                    <div className='align-self-center'>
                        <div>Facial Recognition</div>
                        <div className='success'>Scanned</div>
                    </div>
                    <div className='align-self-center'>
                        <div>Metal Detector</div>
                        <div className='success'>Clear - Unarmed</div>
                    </div>
                    <div className='align-self-center'>
                        <div>Server Room Registration</div>
                        <div className='success'>Cleared - Scanned</div>
                    </div>
                    <div className='align-self-center'>
                        <div>Pod Entrance Scan</div>
                        <div className='success'>Scanned</div>
                    </div>
                    <div className='align-self-center'>
                        <div>Total Time Spent</div>
                        <div className='success'>30 Minutes</div>
                    </div>
                </div>
            )
        })
            : loaded ? visitor.map((obj) => {
                var image = new Image()
                image.src = `data:image/png;base64,${obj.EmployeePhoto}`
                return (
                    <div key={obj.N_LogID} className='Card AllVisitors'>
                        <div className='align-items-center d-flex newCol'>
                            <img src={image.src} className="img-fluid align-self-center" style={{ "width": "4rem", "borderRadius": "100px" }} alt="" />
                            <div className='ms-4'>
                                <div className='head'>{obj.S_Name ? obj.S_Name : "Anonymous"}</div>
                                <div className='temp'>{obj.S_Temperature ? obj.S_Temperature : "Temperature:"}</div>
                            </div>
                        </div>
                        <div className='align-self-center newCol'>
                            <div>Main Entrance Registration</div>
                            <div className='success'>Active - Clear</div>
                        </div>
                        <div className='align-self-center newCol'>
                            <div>Facial Recognition</div>
                            <div className='success'>Scanned</div>
                        </div>
                        <div className='align-self-center newCol'>
                            <div>Metal Detector</div>
                            <div className='success'>Clear - Unarmed</div>
                        </div>
                        <div className='align-self-center newCol'>
                            <div>Server Room Registration</div>
                            <div className='success'>Cleared - Scanned</div>
                        </div>
                        <div className='align-self-center newCol'>
                            <div>Pod Entrance Scan</div>
                            <div className='success'>Scanned</div>
                        </div>
                        <div className='align-self-center newCol'>
                            <div>Total Time Spent</div>
                            <div className='success'>30 Minutes</div>
                        </div>
                    </div>
                )
            }) :
                dummyContent.map((obj) => {
                    return (
                        <div key={obj.id} className='Card AllVisitors'>
                            <div className='align-items-center d-flex'>
                                <img src={face1} className="img-fluid align-self-center" style={{ "width": "4rem", "borderRadius": "10rem" }} alt="" />
                                <div className='ms-4'>
                                    <div className='head'>Anonymous</div>
                                    <div className='temp'>Temperature:NA</div>
                                </div>
                            </div>
                            <div className='align-self-center'>
                                <div>Main Entrance Registration</div>
                                <div className='success'>Active - Clear</div>
                            </div>
                            <div className='align-self-center'>
                                <div>Facial Recognition</div>
                                <div className='success'>Scanned</div>
                            </div>
                            <div className='align-self-center'>
                                <div>Metal Detector</div>
                                <div className='success'>Clear - Unarmed</div>
                            </div>
                            <div className='align-self-center'>
                                <div>Server Room Registration</div>
                                <div className='success'>Cleared - Scanned</div>
                            </div>
                            <div className='align-self-center'>
                                <div>Pod Entrance Scan</div>
                                <div className='success'>Scanned</div>
                            </div>
                            <div className='align-self-center'>
                                <div>Total Time Spent</div>
                                <div className='success'>30 Minutes</div>
                            </div>
                        </div>
                    )
                })}
    </>
    )
}

export default AllVisitors