import JSMpeg from '@cycjimmy/jsmpeg-player';
import React from 'react'
import { useEffect } from 'react';
import { Tab, Tabs } from 'react-bootstrap'
import Camera from './Camera'

const CameraComponent = () => {
    useEffect(() => {
        setTimeout(() => {
            const videoUrl1 = window.globalConfig.camera1
            const videoUrl2 = window.globalConfig.camera2
            new JSMpeg.VideoElement('#camera1', videoUrl1)
            new JSMpeg.VideoElement('#camera2', videoUrl2)
            return () => {
            }
        }, 2000);
    }, [])

    return (
        <div className='CameraComponent'>
            <Tabs
                defaultActiveKey="camera1"
                className="mb-3"
            >
                <Tab eventKey="camera1" title="Camera 1">
                    <Camera canvasID="camera1" />
                </Tab>
                <Tab eventKey="camera2" title="Camera 2">
                    <Camera canvasID="camera2" />
                </Tab>
            </Tabs>
        </div>
    )
}

export default CameraComponent