import React from 'react'
import { useState } from 'react'
import headLogo from './../assets/headLogo.png'
import { Accordion } from 'react-bootstrap'

const Sidebar = () => {
    const [sidenavEffect, setSidenavEffect] = useState(false)
    const sideNavHandler = () => {
        setSidenavEffect((e) => !e)
    }
    return (
        <div className='Sidebar'>
            <div className={sidenavEffect ? 'wrapper open' : 'wrapper close'}>
                <button className='btn btn-light collapseBtn' onClick={sideNavHandler}>
                    <span className="svg-icon svg-icon-2 rotate-180">
                        <svg width="24" height="24" viewBox="0 0 24 24" fill="none"
                            xmlns="http://www.w3.org/2000/svg">
                            <path opacity="0.5"
                                d="M14.2657 11.4343L18.45 7.25C18.8642 6.83579 18.8642 6.16421 18.45 5.75C18.0358 5.33579 17.3642 5.33579 16.95 5.75L11.4071 11.2929C11.0166 11.6834 11.0166 12.3166 11.4071 12.7071L16.95 18.25C17.3642 18.6642 18.0358 18.6642 18.45 18.25C18.8642 17.8358 18.8642 17.1642 18.45 16.75L14.2657 12.5657C13.9533 12.2533 13.9533 11.7467 14.2657 11.4343Z"
                                fill="currentColor" />
                            <path
                                d="M8.2657 11.4343L12.45 7.25C12.8642 6.83579 12.8642 6.16421 12.45 5.75C12.0358 5.33579 11.3642 5.33579 10.95 5.75L5.40712 11.2929C5.01659 11.6834 5.01659 12.3166 5.40712 12.7071L10.95 18.25C11.3642 18.6642 12.0358 18.6642 12.45 18.25C12.8642 17.8358 12.8642 17.1642 12.45 16.75L8.2657 12.5657C7.95328 12.2533 7.95328 11.7467 8.2657 11.4343Z"
                                fill="currentColor" />
                        </svg>
                    </span>
                </button>
                <div>
                    <a href="./index.html">
                        <img alt="Logo" src={headLogo} className="w-25 app-sidebar-logo-default" />
                    </a>
                </div>
                <div className='mt-4'>
                    <Accordion defaultActiveKey="0" flush>
                        <div className='ms-5 mb-3'>Dashboard</div>
                        {window.sideLinks.map((obj) => {
                            return (
                                <Accordion.Item key={obj.allLinks[0].id} eventKey={obj.allLinks[0].id}>
                                    <Accordion.Header>
                                        <div className='navBarIcon'>
                                            <svg version="1.1" id="Capa_1" xmlns="http://www.w3.org/2000/svg" x="0px"
                                                y="0px" width="16" height="16" viewBox="0 0 434.002 434.002" >
                                                <g>
                                                    <g>
                                                        <path fill="#1f273c" d="M95.457,434.002l-33.105-45.076l234.094-171.928L62.352,45.077L95.456,0L360.24,194.459c7.174,5.269,11.41,13.638,11.41,22.539c0,8.9-4.236,17.27-11.41,22.538L95.457,434.002z" />
                                                    </g>
                                                </g>
                                            </svg>
                                        </div>
                                        <div className='ms-2 sideNavLabels'>{obj.allLinks[0].label}</div>
                                    </Accordion.Header>
                                    {
                                        obj.allLinks[0].data.map((obj) => {
                                            return (
                                                <Accordion.Body key={obj.id}>
                                                    <a href={obj.link} className="sideNavLinks">
                                                        {obj.label}
                                                    </a>
                                                </Accordion.Body>
                                            )
                                        })
                                    }
                                </Accordion.Item>
                            )
                        })}
                    </Accordion>
                </div>
            </div>
        </div>
    )
}

export default Sidebar