import React from 'react'
import Slider from 'react-slick'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import SliderCards from './SliderCards';
import { useState } from 'react';
import { allFRLogs } from '../../axios';
import { useEffect } from 'react';
import axios from '../../axios';
import arrow from '../../assets/rightArrow.svg'
import { dummyContent } from '../constants';


function SampleNextArrow(props) {
    const { className, onClick } = props;
    return (
        <img src={arrow} className={className} style={{ transformOrigin: "top" }} onClick={onClick} alt="" />
    );
}

function SamplePrevArrow(props) {
    const { className, onClick } = props;
    return (
        <img src={arrow} style={{ rotate: "180deg ", transformOrigin: "top" }} className={className} onClick={onClick} alt="" />

    );
}
const SliderComponent = () => {

    const [visitor, setVisitor] = useState([])
    const [loaded, setLoaded] = useState(false);
    const [errHandler, setErrHandler] = useState(false)
    useEffect(() => {
        const fetchVisitors = () => {
            axios.post(allFRLogs, {
                "FromDate": "2022-10-03 12:32:10.000",
                "ToDate": "2022-10-03 18:59:52.000"
            }).then((res) => {
                setVisitor(res.data.Result.slice(0, 10))
                // console.log("🚀 slider")
            }).catch((err) => {
                console.log(err);
                setErrHandler(true)
                setLoaded(false)
            }).finally(() => {
                setLoaded(true)
            })
        }
        fetchVisitors()
        const interval = setInterval(() => {
            fetchVisitors()
        }, 5000);
        return () => clearInterval(interval)
    }, [])


    var settings = {
        dots: false,
        infinite: true,
        speed: 500,
        slidesToScroll: 1,
        slidesToShow: 5,
        initialSlide: 0,
        nextArrow: <SampleNextArrow />,
        prevArrow: <SamplePrevArrow />,
        responsive: [
            {
                breakpoint: 1650,
                settings: {
                    slidesToShow: 4,
                }
            },
            {
                breakpoint: 1360,
                settings: {
                    slidesToShow: 3,
                }
            },
            {
                breakpoint: 1050,
                settings: {
                    slidesToShow: 2,
                }
            },
            {
                breakpoint: 740,
                settings: {
                    slidesToShow: 1,
                }
            }
        ]
    };
    return (
        <div className='SliderComponent'>
            <div className='headingLight my-3'> LAST VISITOR ACTIVITY :
                <span className='headingBold'>&nbsp;{visitor[0] ? visitor[0].S_Name === "" ? "Anonymous" : visitor[0].S_Name : "Anonymous"}
                </span>
            </div>

            <Slider {...settings}>
                {errHandler ? dummyContent.slice(0, 10).map((obj) => {
                    return (
                        <div key={obj.id}>
                            <div>
                                <SliderCards />
                            </div>
                        </div>
                    )
                }) : loaded ?
                    visitor.map((obj) => {
                        return (
                            <div key={obj.N_LogID}>
                                <div>
                                    <SliderCards name={obj.S_Name} time={obj.Dt_AccessTime} facial={obj.S_Status} image={obj.EmployeePhoto} />
                                </div>
                            </div>
                        )
                    })
                    :
                    dummyContent.slice(0, 10).map((obj) => {
                        return (
                            <div key={obj.id}>
                                <div>
                                    <SliderCards />
                                </div>
                            </div>
                        )
                    })
                }
            </Slider>
        </div>
    )
}

export default SliderComponent
