import React from 'react'
import face1 from '../../assets/face1.png'

const SliderCards = (props) => {
  var image = new Image()
  image.src = `data:image/png;base64,${props.image}`
  var accessTime = new Date(props.time)
  return (
    <div className='SliderCards'>
      <div className="wrapper">
        <img src={props.image ? image.src : face1} alt="" height="150" className='imgBorder max137' />
      </div>
      <div className='ms-2 d-flex flex-column justify-content-around'>
        <div>
          <div className="head">User Name</div>
          <div className="body">{props.name ? props.name : "Anonymous"}</div>
        </div>
        <div>
          <div className="head">Access Time</div>
          <div className="body">{props.time ? accessTime.getHours() + ":" + accessTime.getMinutes() + ":" + accessTime.getSeconds() : "Unknown"}</div>
        </div>
        <div>
          <div className="head">Facial Recognition</div>
          <div className="body">{props.facial ? props.facial : "Unknown"}</div>
        </div>
      </div>
    </div>
  )
}

export default SliderCards
