  const [pods, setPod] = useState([])
  const [loaded, setLoaded] = useState(false);
  const [errHandler, setErrHandler] = useState(false)
  useEffect(() => {
    const fetchPods = () => {
      axios.post(allFRDevices, {
        "Result": ""
      }).then((res) => {
        setPod(res.data.Result)
        // console.log("🚀 Pods")
      }).catch((err) => {
        console.log(err);
        setErrHandler(true)
      }).finally(() => {
        setLoaded(true)
      })
    }
    fetchPods()
    const interval = setInterval(() => {
      fetchPods()
    }, 7000);
    return () => clearInterval(interval)
  }, [props.callApi])

