import React from 'react'
import { Accordion, useAccordionButton } from 'react-bootstrap'
import AllVisitors from './Slider/AllVisitors'
// import GraphComponent from './Graphs/GraphComponent'
import PieComponent from './PieComponent'
import PodsComponent from './Pods/PodsComponent'
import LogComponent from './LogComponent'
import SliderComponent from './Slider/SliderComponent'
import CameraComponent from './Camera/CameraComponent'
import { useState } from 'react'
import refreshImg from "../assets/refresh.svg"

function CustomToggle({ children, eventKey, accBtn }) {
    const decoratedOnClick = useAccordionButton(eventKey, (e) => {
        accBtn()
    });
    return (
        <button
            type="button"
            style={{
                backgroundColor: 'transparent',
                color: "#05eff4",
                border: "none"
            }}
            onClick={decoratedOnClick}
        >
            {children}
        </button>
    );
}
const Card = (props) => {
    const [refresh, setRefresh] = useState(false)
    const refreshHandler = () => {
        setRefresh((e) => !e)
    }
    const [acc, setAcc] = useState(false)
    const accBtn = () => {
        setAcc((e) => !e)
    }
    return (
        <div className='Card'>
            {props.visitorActivity ? <div className="CardHeightCustom3">
                <Accordion defaultActiveKey="0">
                    <div className='float-end'>
                        <CustomToggle eventKey="1" accBtn={accBtn}>{acc ? "Collapse to View all visitors" : "Expand to View all visitors"}</CustomToggle>
                    </div>
                    <SliderComponent />
                    <Accordion.Item eventKey="1">
                        <Accordion.Body>
                            <AllVisitors />
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
            </div> : ""}
            {props.pods ? <div className='CardHeightCustom2'>
                <div className="headingBold d-flex justify-content-between">
                    <div>{props.head}</div>
                    <div className='d-flex align-items-center'>
                        <div className="blinkerActive mt-0 me-4">Pod Online</div>
                        <div className="blinkerInactive mt-0 me-2">Pod Offline</div>
                        <button className='btn customBtn' onClick={refreshHandler} style={{ padding: "0.2rem 0.5rem" }}>
                            <img src={refreshImg} className="img-fluid" alt="" />
                        </button>
                    </div>
                </div>
                <PodsComponent pods={props.allPods} callApi={refresh} />
            </div> : ""}
            {props.liveCamera ? <div className='CardHeightCustom2'>
                <div className="headingBold">{props.head}</div>
                <CameraComponent />
            </div> : ""}
            {props.accessControl ? <div className='CardHeightCustom'>
                <div className="headingBold d-flex justify-content-between">
                    <div>{props.head}</div>
                    <button className='btn customBtn' onClick={refreshHandler} style={{ padding: "0.2rem 0.5rem" }}>
                        <img src={refreshImg} className="img-fluid" alt="" />
                    </button>
                </div>
                <PieComponent callApi={refresh} />
            </div> : ""}
            {props.serverRoomClimate ? <div className='CardHeightCustom'>
                <div className="headingBold d-flex justify-content-between">
                    <div>{props.head}</div>
                    <button className='btn customBtn' onClick={refreshHandler} style={{ padding: "0.2rem 0.5rem" }}>
                        <img src={refreshImg} className="img-fluid" alt="" />
                    </button>
                </div>
                <LogComponent callApi={refresh} />
            </div> : ""}
        </div>
    )
}


export default Card
