import React from 'react'
// import "./bootstrap.css"
import "./styles.scss"
import Card from './components/Card';
import Sidebar from './components/Sidebar';
import Loader from './components/Loader';
import { useState } from 'react';
import axios, { isAuthorized } from './axios';
import { useEffect } from 'react';
import CryptoJS from 'crypto-js';

const App = () => {
  const [loader, setLoader] = useState(true)
  const [isAuth, setIsAuth] = useState(1)
  setTimeout(() => {
    setLoader(false)
  }, 3310);
  var encrypted = CryptoJS.AES.encrypt("Akshay Ajith", "MAKV2SPBNI99212").toString();
  console.log(encrypted);
  var decrypted = CryptoJS.AES.decrypt(encrypted, "MAKV2SPBNI99212");
  var originalText = decrypted.toString(CryptoJS.enc.Utf8)
  console.log("decrypted", originalText);
  useEffect(() => {
    const checkAuth = () => {
      axios.post(isAuthorized, { "user_id": 1 }).then((res) => {
        localStorage.setItem("status", res.data.Result[0].user_id)
        setIsAuth(localStorage.getItem("status"))
        // console.log("🚀 auth")
      }).catch((err) => {
        console.log(err);
      })
    }
    checkAuth()
    const interval = setInterval(() => {
      checkAuth()
    }, 60000);
    return () => clearInterval(interval)
  }, [])
  const location = window.location.href 
  let loca = location.replace(window.globalConfig.idAuthorization, "")
  console.log(loca);
  return (
    <>
      {isAuth > 0 ?
        <>
          {loader ? <Loader /> : ''}
          <Sidebar />
          <div className="container-fluid App">
            <div className="row">
              <div className="col-12">
                <Card visitorActivity />
              </div>
              <div className="col-12">
                <div className="row">
                  <div className="col-md-12 col-lg-12 col-xl-7">
                    <Card pods
                      head="SERVER ROOM E: DATA HALL B POD 6" />
                  </div>
                  <div className="col-md-12 col-lg-12 col-xl-5">
                    <div className="row d-flex">
                      <div className='col-md-12 col-lg-12 col-xl-6'>
                        <Card liveCamera head="LIVE CAMERA" />
                      </div>
                      <div className="col-md-12 col-lg-12 col-xl-6">
                        <div>
                          <Card accessControl head="ACCESS CONTROL" />
                        </div>
                        <div className='marginCustom'>
                          <Card serverRoomClimate head="ACTIVITY LOG" />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </>
        : window.location = window.globalConfig.loginPage}

    </>
  )
}

export default App
