import axios, { allActivityLogs } from '../axios'
import React, { useEffect, useState } from 'react'
import { OverlayTrigger, Table, Tooltip } from 'react-bootstrap'
import { dummyLogs } from './constants'

const LogComponent = (props) => {

    const [errHandler, setErrHandler] = useState(false)
    const [logs, setLogs] = useState([])
    const [loaded, setLoaded] = useState(false);

    useEffect(() => {
        const getActivityLogs = () => {
            axios.post(allActivityLogs).then((res) => {
                setLogs(res.data)
                // console.log("🚀 Activity Logs")
            }).catch((err) => {
                console.log(err);
                setErrHandler(true)
            }).finally(() => {
                setLoaded(true)
            })
        }
        getActivityLogs()
        const interval = setInterval(() => {
            getActivityLogs()
        }, 4000);
        return () => clearInterval(interval)
    }, [props.callApi])
    const renderTooltip = (name, status, time) => (
        <Tooltip id="button-tooltip">
            <span>{name}</span>
            <span>{status}</span>
            <span>{"Last Online: " + time}</span>
        </Tooltip>
    );
    return (
        <div className='LogComponent'>
            <Table hover size="sm" variant="dark">
                <thead>
                    <tr>
                        <th>USERS/ACTIVITY</th>
                        <th>ACCESS AREA</th>
                        <th>TIME</th>
                    </tr>
                </thead>
                <tbody>
                    {errHandler ? dummyLogs.map((obj) => {
                        return (
                            <tr key={obj.id}>
                                <th>{obj.name}</th>
                                <td>{obj.accessArea}</td>
                                <td>{obj.time}</td>
                            </tr>
                        )
                    }) : loaded ? logs.map((obj) => {
                        var accessTime = new Date(obj.LogTime)
                        var time = accessTime.getHours() + ":" + accessTime.getMinutes() + ":" + accessTime.getSeconds()
                        return (
                            <OverlayTrigger key={obj.ActivityID}
                                placement="top"
                                delay={{ show: 250, hide: 10 }}
                                overlay={renderTooltip(obj.Username, obj.AccessArea, time)}
                            >
                                <tr>
                                    <th>{obj.Username ? obj.Username : "Anonymous"}</th>
                                    <td>{obj.AccessArea ? obj.AccessArea : "NA"}</td>
                                    <td>{time ? time : "NA"}</td>
                                </tr>
                            </OverlayTrigger>
                        )
                    }) :
                        dummyLogs.map((obj) => {
                            return (
                                <tr key={obj.id}>
                                    <th>{obj.name}</th>
                                    <td>{obj.accessArea}</td>
                                    <td>{obj.time}</td>
                                </tr>
                            )
                        })
                    }
                </tbody>
            </Table>
        </div>
    )
}

export default LogComponent
