import axios from '../axios'
import React, { useEffect, useState } from 'react'
import { activeInactive } from '../axios'



const PieComponent = (props) => {
    const [pie, setPie] = useState([])
    const [loaded, setLoaded] = useState(false);
    const [errHandler, setErrHandler] = useState(false)
    useEffect(() => {
        const fetchPie = () => {
            axios.post(activeInactive).then((res) => {
                setPie(res.data.Result)
                // console.log("🚀")
            }).catch((err) => {
                console.log(err);
                setErrHandler(true)
            }).finally(() => {
                setLoaded(true)
            })
        }
        fetchPie()
        const interval = setInterval(() => {
            fetchPie()
        }, 6000);
        return () => clearInterval(interval)
    }, [props.callApi])

    const pActive = (pie ? pie.ActiveDevices * 100 : 0) / (pie ? pie.ActiveDevices : 0 + pie ? pie.InActiveDevices : 0),
        pInactive = (pie ? pie.InActiveDevices * 100 : 0) / (pie ? pie.ActiveDevices : 0 + pie ? pie.InActiveDevices : 0)

    return (
        <div className='PieComponent'>
            <div className="pie" style={{ "--pa": pActive, "--pi": pInactive }} >
                <div className='totalNumber'>
                    {(pie ? pie.ActiveDevices : 0) + (pie ? pie.InActiveDevices : 0)}
                </div>
            </div>
            <div className="d-flex justify-content-between db">
                <div className="blinkerActive">Active Cameras: <b>{pie ? pie.ActiveDevices : 0}</b></div>
                <div className="blinkerInactive">InActive Cameras: <b>{pie ? pie.InActiveDevices : 0}</b></div>
            </div>
        </div>
    )
}

export default PieComponent
