import axios from "axios";
const baseURL = window.globalConfig.baseUrl;

export const allFRDevices = window.globalConfig.allFRDevices;
export const allFRLogs = window.globalConfig.allFRLogs;
export const allActivityLogs = window.globalConfig.allActivityLogs;
export const isAuthorized = window.globalConfig.authStatus;
export const activeInactive = window.globalConfig.activeInactiveDevices;
const instance = axios.create({
  baseURL: baseURL,
  headers: {
    "Content-Type": "application/x-www-form-urlencoded",
  },
});

export default instance;
